import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import Login from './views/Login'
import Photography from './views/Photography'
import Videography from './views/Videography'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/photography',
      name: 'photography',
      component: Photography
    },
    {
      path: '/videography',
      name: 'videography',
      component: Videography
    }
  ]
})
